$min_width: 320px;
$max_width: 1800px;
$min_font: 14px;
$max_font: 16px;

html {
  @include fluid-type($min_width, $max_width, $min_font, $max_font);
}

button, 
select, 
html, 
textarea, 
input, 
.error-info textarea.changed.ng-dirty, 
.error-info select.changed.ng-dirty {
  font-family: "overpass";
}

.page {
    @include rem('padding-left', $global-padding);
    @include rem('padding-right', $global-padding);
}

.page-wrap { z-index: auto; }

.main-content .row { margin-bottom: 0; }
.pointer { cursor: pointer !important; }
.row + .row, .row.row-tall { @include rem('margin-top', $global-margin); }
.row.row-short { @include rem('margin-top', $global-padding); }
.row.offset { @include rem('margin-left', 40px); }

input.rounded {
	border: 4px solid #ebeef0;
	@include rem('padding', 6px 8px);
	line-height: 1.2;
	border-radius: 10px;
}

.insights a {
  &:hover {
    cursor: pointer;
    text-decoration: none;
  }
  &[disabled] {
    cursor: not-allowed;
    pointer-events: none;
    color: #8d8d8d;
    opacity: .65;
  }
}

.band.band-wispy {
	background: url("../static/images/bg1.jpg");
	background-size: cover;
}

.chart-container p {
    @include font-size(14px);
}

.success {
  color: green;
}

.fail {
  color: red;
}

.portal-content-area, .page-wrap .main-content {
  background: none;
}

.insights .portal-content-area, .insights .main-content { background: transparent; }

.dashboard-navigation-container {
    @include media-query (sm) {
        float: left;
        width: 18%;
    }
}

.masthead {
  background: none;
}

.page-wrap .insights-main-content {
    padding-top: 0;
    background: #fff;

    @include media-query (sm) {
        float: left;
        width: 82%;
    }
}

.content-wrap, .content-wrapper {
  //padding-left: 15px;
  //padding-right: 15px;
  @include rem('padding', 0 $global-margin $global-margin);

  @include media-query (sm) {
    @include rem('padding', 0 $global-margin $global-margin);
  }
}

.page-wrap .insights-main-content.full-width {
  float: none;
  width: 100%;
}

.page-standard {
	@include rem('padding', 40px 0);
}

.page-wrap.container {
	padding: $global-padding;

	@include media-query (sm) {
		padding: 0;
	}
}

.ellipsis-overflow {
  text-overflow: ellipsis;
  overflow: hidden;
}

.band-nopad {
	padding: 0;
}

.file-video {
    width: 100%;
    padding-bottom: 52.65%;
    position: relative;

    iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.sep {
    display: inline-block;
    @include rem('margin', 0 6px);
}
.cursor:hover { cursor: pointer; }

.pointer-events-auto {
    pointer-events: auto !important;
}

a.link {
  color: $blue;
  text-decoration: underline;

  &:hover {
    color: darken($blue, 10);
    text-decoration: underline;
  }
}

.utility-wrap{ z-index: 100; }