.incident-lite {
  display: flex;
  flex-flow: row wrap; 
  align-items: center;
  margin: 0;
  background: $gray-light;

  .icon,
  .supporting-text { @include rem('padding', $global-padding); }

  // Icon layout
  .icon { 
    flex: 0; 
    padding-right: 0;
  }

  // Count layout
  .count {
    flex: 0 1 0;
    @include rem('padding', 0 $global-padding);
    white-space: pre;

    @include media-query(xs){ 
      text-align: left; 
      border-right: 1px solid $gray;
    }
  }

  .supporting-text {
    line-height: 1.4;

    @include media-query(xs){ flex: 3; }
  }
}


// Number styling
.incident-lite {

  .icon i.fa {
    @include font-size(38px); 
    line-height: 1;
  }

  .num a { color: $red; }
}


// Supporting text
.incident-summary {
  display: flex;
  flex-flow: column wrap;
  @include rem('margin', auto -15px);

  @include media-query(sm){ flex-direction: row; }

  .cell { @include rem('padding', $global-padding); }

  .counts {
    display: inline-flex;
    justify-content: center;

    @include media-query(sm){ 
      flex: 0 1 140px;
      flex-direction: column;
      @include rem('padding', 0 $global-padding);
      border-right: 1px solid $gray;
      justify-content: flex-start;
    }

    @include media-query(md){
      flex: 1;
    }
  }

  .supporting-text { flex: 3; }
}


.incident-summary {

  p { margin: 0; }

  .num { 
    margin: 0;
    @include font-size(28px);
    font-weight: 700;
    line-height: 1;
    word-break: break-all; 
  
    &.text-danger { color: $red; }
  }

  .count { 
    @include rem('padding', 0 $global-padding);
    text-align: center;

    @include media-query(sm){ 
      text-align: left; 
      @include rem('padding', $global-padding 0);
    }
  }

  .count + .count { 
    border-left: 1px solid $gray; 

    @include media-query(sm){
      border-left: none;
      border-top: 1px solid $gray; 
    }
  }
}

